.body {
  background-color: black;
  color: white;
  max-width: 100%;
  /* height: 120vh; */
  width: 100%;
  /* font-family: fantasy; */
}

.head {
  display: flex;
  justify-content: center;
  
  font-family: fantasy;
  font-size: 1.8rem;
  font-weight: 700;
  padding-top: 2rem;
  margin-right: 2.5rem;
  margin-left: 2.5rem;
  /* position: sticky; */
  top: 0;
  background-color: transparent;
}

#dog {
  align-content: space-between;
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 20px;
  padding-top: 20px;
  /*  */
  max-height: 22rem;
  min-width: 20rem;
  min-height: 20rem;
}

#buttons {
  margin-top: 4rem;
  justify-content: center;
  justify-content: space-between;
}

#inspiration {
  margin-left: 2rem !important;
}

.quote {
  justify-content: center;
  display: block;
  margin-left: 4rem;
  margin-right: 4rem;
  color: white;
  font-weight: 200;
  font-family: cursive;
}

.name {
  font-family: fantasy;
  justify-content: center;
  font-weight: 700 !important;
}

.chaz {
  justify-content: center;
  display: block;
  margin-left: 4rem;
  margin-right: 4rem;
  padding-bottom: 4rem;
  color: red;
}

.kanye {
  justify-content: center;
  display: block;
  margin-left: 4rem;
  margin-right: 4rem;
  color: yellow;
  opacity: 1;
}

#kGray {
  display: block;
  justify-content: center;
  margin-left: 7rem  !important;
}

.grey-after:after {
  /* color: rgb(50, 50, 40); */
  color: purple;
  justify-content: center;
}

.chuckbutton {
  padding: auto 4;
  justify-content: center;
}

/* Responsive Design */
/* 320 - 812 px */
@media only screen and (min-device-width: 320px) and (max-device-width: 812px) {
  #dog {
    max-height: 20rem !important;
   }
   .head {
     font-size: 1.8rem;
     font-family: fantasy;
    }
   .name {
     font-size: 1.5rem;
     font-weight: 200;
   }
}

/* iPhone 5 */
@media only screen and (min-device-width: 320px) and (max-device-width: 568px) {
  .head {
    margin-left: 1.8rem;
  }

  #dog {
    max-width: 18rem;
  }

  .chaz,
  .kanye {
    margin-left: 2rem !important;
    margin-right: 2rem !important;
    font-size: 1rem;
  }

  .quote {
    margin-bottom: 2rem;
    margin-left: 2rem;
    margin-right: 0.8rem;
    font-size: 2rem;
    color: white;
  }
}

/* iPhone 6/7/8 */
@media only screen and (min-device-width: 375px) and (max-device-width: 667px) {
  .head {
    margin-left: 2.5rem;
  }

  #dog {
    max-width: 22rem !important;
    max-height: 30rem;
  }

  .chaz,
  .kanye {
    margin-left: 2rem !important;
    margin-right: 2rem !important;
    font-size: 1rem;
  }

  .quote {
    margin-bottom: 2rem;
    margin-left: 2rem;
    margin-right: 0.8rem;
    font-size: 2rem;
  }
}

/* iPhone 6/7/8 Plus */
@media only screen and (min-device-width: 414px) and (max-device-width: 736px) {
  .head {
    /* font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; */
    font-size: 1.8rem;
  }

  #dog {
    max-width: 25rem;

  }

  .chaz,
  .kanye {
    margin-left: 2rem !important;
    margin-right: 2rem !important;
    font-size: 1rem;
  }

  .quote {
    margin-bottom: 2rem;
    margin-left: 2rem;
    margin-right: 0.8rem;
    font-size: 2rem;
  }
}

/* iPhone 10 */
@media only screen and (min-device-width: 375px) and (max-device-width: 812px) {
  .head {
    margin-left: 2.5rem;
  }

  #dog {
    max-width: 25rem;
  }

  .chaz,
  .kanye {
    margin-left: 2rem !important;
    margin-right: 2rem !important;
    font-size: 1rem;
  }

  .quote {
    margin-bottom: 2rem;
    margin-left: 2rem;
    margin-right: 0.8rem;
    font-size: 2rem;
  }
}
